body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
  font-family: 'Helvetica';
  src: url("/assets/fonts/HelveticaRegular/HelveticaRegular.woff") format("woff");
  font-weight: normal;
  font-style: normal; }

/* fonts */
/* colors */
/* common styles */
body {
  color: #122d4b;
  font-family: 'Helvetica', Arial, sans-serif;
  font-size: 16px;
  letter-spacing: .3px; }

header,
footer {
  background-color: #122d4b; }

header {
  position: relative;
  z-index: 1;
  height: 70px;
  box-shadow: 0 2px 22px rgba(69, 69, 69, 0.4); }

footer {
  height: 144px; }

.main {
  padding: 50px 0;
  background-color: #f6f6f5; }

.heading {
  margin-bottom: 24px; }

.row {
  display: -webkit-flex;
  display: flex; }

.side {
  width: 160px;
  margin-right: 30px; }
  .side button {
    width: 100%;
    margin-bottom: 8px;
    text-align: left; }

/* typography */
button {
  height: 36px;
  padding: 10px;
  border: none;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: 0 0 7px rgba(113, 113, 113, 0.36);
  font-family: "HelveticaBold", Arial, sans-serif;
  font-size: 14px; }
  button.active {
    background-color: #e4bd7d;
    color: #fff;
    text-shadow: 0px 2px 3px rgba(0, 0, 0, 0.2); }

table {
  min-width: 730px;
  background-color: #fff;
  box-shadow: 0 0 7px rgba(113, 113, 113, 0.36);
  border-collapse: collapse;
  border-radius: 4px; }

td {
  padding: 15px 30px; }
  td:first-child {
    font-weight: bold;
    color: #dc5162; }

tr:nth-child(2n-1) td {
  background-color: #efefef; }

th {
  padding: 15px 30px;
  text-align: left;
  font-family: "HelveticaBold", Arial, sans-serif; }

/* blocks */
.word {
  display: table;
  margin: 0 auto; }
  .word-title {
    font-family: "HelveticaBold", Arial, sans-serif;
    font-size: 18px; }
  .word-explanation {
    margin-top: 10px;
    font-size: 14px; }

